@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&family=Ubuntu:wght@500&display=swap);
.main-card-div{
    text-align: center;
    justify-content: center;
    display: flex;
    align-items: center;
}
.subscription-card{
    width: 100%;
    height: 100%;
    background: #F7F8FF;
    border-radius: 10px;
    border: none;
}
.plan-text{
    font-family: 'Poppins','Light 400';
font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 30px;
color: #000000;
text-align: center;
}
.para-div-main{
    margin-left: 22px;
}
.offer-div{
    width: 195px;
padding: 14px;
margin: 1rem auto;
text-align: center;


background: #FFFFFF;
border-radius: 30px;
}
.rectangle-img-div{
    width: 50px;
    height: auto;
}

.rectangle-image{
    width: 100%;
    height: auto;
    box-shadow: linear-gradient(270deg, #FF7878 0%, #FF1E1E 100%) 0px 8px 24px;
}

.offer-para{
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 24px;
color: #000000;
margin-top: 0;
margin-bottom: 0;
}

.offer-doller{
    color: #3751FF;
}

.offer-list{
    width: 73%;
    margin: 0 auto;

}

.offer-list ul li{
    font-family: 'Poppins';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 43px;
align-items: center;

color: #263238;
}

.upgrade-btn{
    background: #3751FF;
border-radius: 30px;
width: 174px;
color: white;
}

.button-div{
    text-align: center;
}
.ant-tabs-card > .ant-tabs-nav .ant-tabs-tab-active{
    background-color: #3751FF !important;
    color: #fff;
}
.tabpannel, .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
    color: #fff !important;
    text-shadow: currentcolor 0px 0px 0.25px;
}


.Edit-button{
    background-color: #E5E5E5 !important;
    color: #494949;
}
.delete-button{
    background-color:#FF8C00;
    color: #fff;
}
.Pending{
    color: #FF3737;
}
.Complete{
    color: #048E2B;
}
.InProgress{
    color: #FF8C00;
    ;
}
.table-td-background{
    background-color: #F5F6F8;
    width: 70%;
}
.ant-select:not(.ant-select-customize-input) .ant-select-selector{
    border: none !important;
    background-color: #F5F6F8 !important
}
.select-drop{
    width: -webkit-fill-available;
}
.option-drop.ant-select-selector{
    background-color: #F5F6F8 !important;
}
table {
    border-collapse: unset !important;
}
.setting-right-btn{
    float: right;
}
.input-width{
    width: 250% !important;
    background-color: rgb(233, 236, 241) !important
}
.table-responsives{
    overflow-x: auto !important;
}
.form-align-set{
    flex-direction: row !important;
    align-items: center;
}
.form-align-set label{
    width: 200px !important;
}
.form-input-field{
    margin-left: 10px;
}

